	module.exports = {"data":{"markdownRemark":{"html":"<p>私は一組の布の靴を買いました、そして、私たちは古い八方靴であると冗談を言いました。今日、市場には多くのものがあり、すべてがあり、あらゆる種類の輸入された国内製品があります。これは、経済が発展し、市場が発展していることを示しています。しかし、文化的な観点からは、それはまだ高くはありません。市場は単なる商品の販売と積み上げではなく、あらゆる文化の集積です。おそらく社会の経済発展、商品を豊かにすることは容易ではなく、より高い経済文化を達成することはさらに困難です。</p>\n<p>文化は暴力で解決できるものではなく、歴史を持ち、国家の精神的、文化的レベルに結びついていなければなりません。最近の綿、タバコ、アルコールの偽造は人々にこれを深く感じさせました。物資の成長に文化をいかに成長させるかは、大きな社会開発課題であり、社会はバランスのとれた方法で発展することができます。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0141.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0141.md","frontmatter":{"title":"8月29日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0141.md absPath of file >>> MarkdownRemark"}}

